import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);


export default new Vuex.Store({
    state: {
        allMeasurements: [],
        historicalMeasurements: JSON.parse(localStorage.getItem("historicalMeasurements")) !== null ? JSON.parse(localStorage.getItem("historicalMeasurements")) : [] 
    },
    mutations: {
        measurementAdded(state, measurement) {
            state.allMeasurements.push(measurement);
        },
        resetMeasurementsTable(state) {
            state.allMeasurements = [];
        },
        historicalMeasurementsAdded(state, measurement) {
            state.historicalMeasurements.push(measurement);
        },
        historicalMeasurementDelete(state, measurements) {
            state.historicalMeasurements = measurements;
        }
    }
});