<template>
    <div class="mt-5 ml-5 mr-5 p-2 table-overflow">
        <h4 class="mb-2"> Сите претходни мерења: </h4>

        <table class="table bg-white">
            <thead>
                <tr>
                    <th scope="col"> Име на мерење </th>
                    <th scope="col"> Должина </th>
                    <th scope="col"> Висина </th>
                    <th scope="col"> Вкупно површина (m<sup>2</sup>) </th>
                    <th scope="col"> Датум на мерење </th>
                    <th scope="col"> </th>
                </tr>
            </thead>
            <tbody v-if="historicalMeasurements.length > 0" >
                <tr v-for="measurement in historicalMeasurements" :key="measurement.Id">
                    <td class="text-center"> {{ measurement.Name }} </td>
                    <td class="text-center"> <span class="font-weight-bold"> {{ measurement.Length }} </span> {{ measurement.UnitMeasurement === 'centimetri' ? (measurement.Length === '1' ? ' центиметар' : ' центиметри') : (measurement.Length === '1' ? ' метар' : ' метри') }} </td>
                    <td class="text-center"> <span class="font-weight-bold"> {{ measurement.Height }} </span> {{ measurement.UnitMeasurement === 'centimetri' ? (measurement.Length === '1' ? ' центиметар' : ' центиметри') : (measurement.Length === '1' ? ' метар' : ' метри') }}  </td>
                    <td> 
                        <button class="btn btn-info results-width"
                            @click="addToMeasurementCalculate(measurement)" > 
                            {{ measurement.MeasurementResult }} 
                        </button> 
                        <i class="ml-3 font-icons float-right fa" 
                            :class="{'fa-check-square text-success':(measurementsCalculate.includes(measurement)), 'fa-times text-danger':(!measurementsCalculate.includes(measurement))}"/>
                    </td>
                    <td class="text-center"> {{ (measurement.DateOfMeasurement) }} </td>
                    <td class="text-center">  
                        <button class="btn btn-danger"
                            @click="deleteMeasurement(measurement)" > 
                            Избриши
                        </button> 
                    </td>
                </tr>
                <tr> 
                    <td colspan="6" class="text-center"> Вкупна површина на сите измерени предмети: <span class="font-weight-bold"> {{ resultOfHistoricalMeasurements }} </span> (m<sup>2</sup>) </td>
                </tr>
                <tr v-if="resultOfCalculatedSelectedMeasurements !== 0"> 
                    <td colspan="6" class="text-center"> Површина на селектираните елементи: <span class="font-weight-bold"> {{ resultOfCalculatedSelectedMeasurements }} </span> (m<sup>2</sup>) </td>
                </tr>
                <tr v-else> 
                    <td colspan="6" class="text-center font-weight-bold"> Селектирајте предмети за да пресметате површина </td>
                </tr>
            </tbody>
            <tbody v-else >
                <tr> <td colspan="6" class="text-center font-weight-bold"> Нема претходни мерења во системот </td> </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default { 
    name: 'today-measurements',
    data() {
        return {
            resultOfHistoricalMeasurements: 0,
            measurementsCalculate: [],
            resultOfCalculatedSelectedMeasurements: 0
        }
    },
    computed:  mapState([
        'historicalMeasurements'
    ]),
    methods: {
        calculateTotalMeasurements() {
            this.resultOfHistoricalMeasurements = this.historicalMeasurements.map(el => { return parseFloat(el.MeasurementResult) }).reduce((total, num) => { return total+num }, 0);
        },
        addToMeasurementCalculate(measurement) {
            var measurementAlreadyInCalculate = this.measurementsCalculate.find(el => {return el.Id === measurement.Id});
            console.log('measurementAlreadyInCalculate: ', measurementAlreadyInCalculate);
            if(measurementAlreadyInCalculate === undefined) {
                this.measurementsCalculate.push(measurement);
            } else {
                this.measurementsCalculate = this.measurementsCalculate.filter(el => {return el.Id !== measurement.Id});
            }
            this.resultOfCalculatedSelectedMeasurements = this.measurementsCalculate.map(el => { return parseFloat(el.MeasurementResult) }).reduce((total, num) => { return total+num }, 0);
            console.log('this.measurementsCalculate: ', this.measurementsCalculate);
        },
        deleteMeasurement(measurement) {
            if (window.confirm("Дали сакаш да го избришеш селектираното мерење ?")) {
                var historicalMeasurementsRemoved = JSON.parse(localStorage.getItem("historicalMeasurements")).filter(el => { return el.Id !== measurement.Id });
                localStorage.setItem("historicalMeasurements", JSON.stringify(historicalMeasurementsRemoved)); 
                this.$store.commit('historicalMeasurementDelete',  historicalMeasurementsRemoved);
            }
        }
    },
    updated() {
        this.calculateTotalMeasurements();
    },
    created() {
        if(this.historicalMeasurements.length !== 0) {
            this.calculateTotalMeasurements();
        }
    }
}
</script>


<style scoped>
.table-overflow {
    overflow: auto;
}
table {
    border: 1px solid #DEE2E6;
}
thead {
    background: #000000;
}
thead th {
    color: white;
}
td {
    border-right: 1px solid #DEE2E6;
}
.font-icons {
    font-size: 21px;
}
.results-width {
    width: 80%;
}
</style>
