<template>
    <div class="mt-5 ml-5 mr-5 p-2 table-overflow">
        <div class="col-md-12 mb-1">
            <div class="row">
                <div class="col-md-6">
                    <h4> Денешни мерења: </h4>
                </div>
                <div class="col-md-6">
                    <button class="btn btn-primary float-right" @click="resetTable"> Ресетирај табела </button>
                </div>
            </div>
        </div>
        <table class="table bg-white">
            <thead>
                <tr>
                    <th scope="col"> Име на мерење </th>
                    <th scope="col"> Должина </th>
                    <th scope="col"> Висина </th>
                    <th scope="col"> Вкупно површина (m<sup>2</sup>) </th>
                    <th scope="col"> Датум на мерење </th>
                </tr>
            </thead>
            <tbody v-if="allMeasurements.length > 0" >
                <tr v-for="measurement in allMeasurements" :key="measurement.Id">
                    <td> {{ measurement.Name }} </td>
                    <td> <span class="font-weight-bold"> {{ measurement.Length }} </span> {{ measurement.UnitMeasurement === 'centimetri' ? (measurement.Length === '1' ? ' центиметар' : ' центиметри') : (measurement.Length === '1' ? ' метар' : ' метри') }} </td>
                    <td> <span class="font-weight-bold"> {{ measurement.Height }} </span> {{ measurement.UnitMeasurement === 'centimetri' ? (measurement.Length === '1' ? ' центиметар' : ' центиметри') : (measurement.Length === '1' ? ' метар' : ' метри') }}  </td>
                    <td> {{ measurement.MeasurementResult }} </td>
                    <td> {{ (measurement.DateOfMeasurement) }} </td>
                </tr>
                <tr> <td colspan="5" class="text-center"> Вкупна површина на сите измерени предмети: <span class="font-weight-bold"> {{ resultOfAllMeasurements }} </span> (m<sup>2</sup>) </td> </tr>
            </tbody>
            <tbody v-else >
                <tr> <td colspan="5" class="text-center font-weight-bold"> Нема денешни мерења во системот </td> </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default { 
    name: 'today-measurements',
    data() {
        return {
            resultOfAllMeasurements: 0
        }
    },
    computed:  mapState([
        'allMeasurements'
    ]),
    methods: {
        calculateTotalMeasurements() {
            this.resultOfAllMeasurements = this.allMeasurements.map(el => { return parseFloat(el.MeasurementResult) }).reduce((total, num) => { return total+num }, 0);
        },
        resetTable() {
            this.$store.commit('resetMeasurementsTable');
        }
    },
    updated() {
        this.calculateTotalMeasurements();
    },
    created() {
        if(this.allMeasurements.length !== 0) {
            this.calculateTotalMeasurements();
        }
    }
}
</script>


<style scoped>
.table-overflow {
    overflow: auto;
}
table {
    border: 1px solid #DEE2E6;
}
thead {
    background: #000000;
}
thead th {
    color: white;
}
td {
    border-right: 1px solid #DEE2E6;
}
</style>
