import Vue from "vue";
import VueRouter from "vue-router";

import MainPage from '../views/MainPage';
import HistoricalMeasurementsPage from '../views/HistoricalMeasurementsPage';
import ProjectsPage from '../views/ProjectsPage';

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        component: MainPage,
        name: 'MainPage'
    },
    {
        path: "/prethodni-merenja",
        component: HistoricalMeasurementsPage,
        name: 'HistoricalMeasurementsPage'
    },
    {
        path: "/proekti",
        component: ProjectsPage,
        name: 'ProjectsPage'
    },
];

export default new VueRouter({ 
    routes
});
