<template>
      <div class="mt-5 ml-5 border p-2 bg-white">
            <div class="form-group" >
                  <label class="font-weight-bold"> Име на мерење: </label>
                  <input type="text" class="form-control input-width" placeholder="Внеси име на мерениот елемент" v-model="calculatingForm.Name"
                        :class="{'border-error':(emptyFields === true && calculatingForm.Name === '')}" > 
            </div>
            <div class="form-group">
                  <input type="radio" id="cm" name="mernaEdinica" value="centimetri" v-model="calculatingForm.UnitMeasurement">
                  <label class="ml-2" for="cm"> Центиметри (cm) </label>
                  <input type="radio" id="m" name="mernaEdinica" value="metri" class="ml-5" v-model="calculatingForm.UnitMeasurement">
                  <label class="ml-2" for="m"> Метри (m) </label>
            </div>
            <div class="form-group" >
                  <label class="font-weight-bold"> Должина: </label>
                  <input type="text" class="form-control input-width" placeholder="Внеси должина на елементот" v-model="calculatingForm.Length"
                        :class="{'border-error':(emptyFields === true && calculatingForm.Length === '')}" >
            </div>
            <div class="form-group" >
                  <label class="font-weight-bold"> Висина: </label>
                  <input type="text" class="form-control input-width" placeholder="Внеси висина на елементот" v-model="calculatingForm.Height"
                        :class="{'border-error':(emptyFields === true && calculatingForm.Height === '')}" >
            </div>
            <button class="btn btn-primary" @click="calculateMeasurement"> Пресметај </button>
            <br>
            <label> Резултат од мерењето: <span class="font-weight-bold"> {{ calculatingForm.MeasurementResult }}  (m<sup>2</sup>) </span> </label>
      </div>
</template>

<script>
import { mapState } from 'vuex';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';

export default { 
      name: 'calculate-form',
      data() {
            return {
                  calculatingForm: {
                        Name: '',
                        UnitMeasurement: 'centimetri',
                        Length: '',
                        Height: '',
                        MeasurementResult: '',
                        DateOfMeasurement: '',
                        Id: ''
                  },
                  emptyFields: false,
                  toastrMessage: ''
            }
      },
      methods: {
            calculateMeasurement() {
                  if( this.calculatingForm.Name === '' || this.calculatingForm.Length === '' || this.calculatingForm.Height === '' ){
                        this.$toast.open({
                              message: 'Селектираните полиња се задолжителни !',
                              type: 'error',
                              position: 'top-right'
                        });
                        return this.emptyFields = true;
                  }

                  if( isNaN(this.calculatingForm.Length) || isNaN(this.calculatingForm.Height) ){
                        this.$toast.open({
                              message: 'Должината и Висината мора да се бројки !',
                              type: 'error',
                              position: 'top-right'
                        });
                        return this.toastrMessage = 'Должината и Висината мора да се бројки';
                  }
                  // calculate measurement in m2
                  var length =  this.calculatingForm.UnitMeasurement === 'centimetri' ? parseInt(this.calculatingForm.Length) * 0.01 : parseInt(this.calculatingForm.Length);
                  var height =  this.calculatingForm.UnitMeasurement === 'centimetri' ? parseInt(this.calculatingForm.Height) * 0.01 : parseInt(this.calculatingForm.Height);

                  var result = length * height;
                  this.calculatingForm.MeasurementResult = `${result} ${result !== 1 ? 'метри квадратни' : 'метар квадратен'}`;

                  this.calculatingForm.DateOfMeasurement = moment().format('llll');
                  this.calculatingForm.Id = uuidv4();

                  this.$store.commit('measurementAdded', Object.assign({}, this.calculatingForm));

                  this.$store.commit('historicalMeasurementsAdded', Object.assign({}, this.calculatingForm) );

                  localStorage.setItem("historicalMeasurements", JSON.stringify(this.historicalMeasurements)); // add in previous measurements in LocalStorage
            }
      },
      computed: mapState([
            'allMeasurements',
            'historicalMeasurements'
      ])
}
</script>


<style scoped>
.border-error {
      border: 1px solid red;
}
</style>
